import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import { ImgBlock } from '../../components/imgBlock';
import Loader from 'react-loader-spinner'
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import { readRemoteFile } from "react-papaparse";
import param from "../../param";


function Honors(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vTNRsS8-h7of-vQ5o49jTlD6rfuBAv1ANhDKsVk-1_bQIYOJ11ndU4km3xrXtyC7hIWIKBy6EMHw48U/pub?gid=0&single=true&output=csv';
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          

          // get all years
          var years = []
          for (let i = 1; i < results.data.length; i++) {
            if (years.indexOf(results.data[i][0]) == -1) {
              years.push(results.data[i][0])
            }
          }
          years.sort()
          

          //
          var newData = []
          for (let y = years.length - 1; y > 0; y--) {
            let honorList = []
            for (let i = 1; i < results.data.length; i++) {
              if (results.data[i][0] == years[y]) {
                let photo = 'honor.png'
                if (results.data[i][3] != '') photo = results.data[i][3]
                honorList.push({
                  title: results.data[i][1],
                  award: results.data[i][2],
                  image: param.photoUrl + photo,
                })
              }
            }
            newData.push({
              year: years[y],
              list: honorList,
            })
          }
          
          setCsvData(newData);
          setGetData(true);
        },
      });
      
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{tw:'獎項',en:'Honor'}} />
        <ResearchSection data={csvData} />
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{tw:'獎項',en:'Honor'}} />
        <div className='text-center my-5'>
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}



export { Honors };



function Section(props) {


  return (
    <div className='section'>
      <p className='year'>{props.year}</p>
      <hr />
      <Carousel
        additionalTransfrom={0}
        arrows
        autoPlaySpeed={3000}
        centerMode={false}
        className=""
        containerClass="container-with-dots"
        dotListClass=""
        draggable
        focusOnSelect={false}
        infinite
        itemClass=""
        keyBoardControl
        minimumTouchDrag={80}
        renderButtonGroupOutside={false}
        renderDotsOutside={false}
        responsive={{
          desktop: {
            breakpoint: {
              max: 3000,
              min: 1024
            },
            items: 3,
            partialVisibilityGutter: 40
          },
          mobile: {
            breakpoint: {
              max: 464,
              min: 0
            },
            items: 1,
            partialVisibilityGutter: 30
          },
          tablet: {
            breakpoint: {
              max: 1024,
              min: 464
            },
            items: 2,
            partialVisibilityGutter: 30
          }
        }}
        showDots={false}
        sliderClass=""
        slidesToSlide={1}
        swipeable
      >
        {
          props.list.map((d) => (
            <SectionCard title={d.title} award={d.award} image={d.image} />
          ))
        }
      </Carousel>
    </div>
  )
}

function SectionCard(props) {

  return (
    <div className='mx-5' style={{ width: '90%' }}>
      {
        (() => {
          if (props.image == '') {
            return (
              <ImgBlock src={'https://nclab-me-ncku.github.io/photo/honor.png'} />
            )
          } else {
            return (<ImgBlock src={props.image} />)
          }
        })()
      }

      <p className='title'>{props.title}</p>
      <p className='award'>{props.award}</p>
    </div>
  )
}

function ResearchSection(props) {
  let data = props.data

  return (
    <div className='honor'>
      {
        data.map((d) => (
          <Section year={d.year} list={d.list} />
        ))
      }
    </div>

  )
}