import React, { Component, useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import Loader from 'react-loader-spinner'
import { Container, Row, Col } from "react-bootstrap";
import photoData from "./data";
import { readRemoteFile } from "react-papaparse";
import param from "../../param";

function Photo(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vSzmfEFBS7uQSVPeFyDP7r9vsDx8qyMBnlgmzAvLx-DN-UAWB0Qki5JfTHL7X-oDOdSkCG4xymsc8Xm/pub?gid=0&single=true&output=csv';
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          var newPhotoData = []
          for (let i = 1; i < results.data.length; i++) {
            newPhotoData.push({
              image: param.photoUrl + results.data[i][0],
              text: results.data[i][1],
            })
          }     
          setCsvData(newPhotoData);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{tw:'照片',en:'Photo'}} />
        <PhotoSection data={csvData} />
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{tw:'照片',en:'Photo'}} />
        <div className='text-center my-5'>
          <Loader type="Grid" color="#403E3E" height={80} width={80} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}



export { Photo };

function PhotoSection(props) {
  let data = props.data
  return (
    <Container fluid>
      <Row>
        {
          data.map(d => (
            <Col xs={12} md={6}>
              <PhotoCard src={d.image} text={d.text} />
            </Col>

          ))
        }
      </Row>
    </Container>
  )
}

// 16:9
function PhotoCard(props) {
  return (
    <div>
      <div
        style={{
          width: "100%",
          paddingBottom: "56.25%",
          height: "0",
          position: "relative",
        }}
      >
        <div
          style={{
            position: "absolute",
            top: "0",
            bottom: "0",
            right: "0",
            left: "0",
          }}
        >
          <div
            style={{
              height: "100%",
              overflow: "hidden",
              display: "flex",
              justifyContent: "center",
              backgroundColor: '#f0f0f0',
              borderRadius: '5px'
            }}
          >
            <img style={{ height: "100%" }} src={props.src} />
          </div>
        </div>
      </div>
      <div>
        <p className='text-center' style={{fontSize:'1.5rem',color:'#403E3E'}}>{props.text}</p>
      </div>
    </div>

  )
}